.navbar{
   display: flex;
   height: 80px;
   justify-content: center;
   align-items: center;
   position: sticky;
   top: 0;
   background-color: whitesmoke;
   z-index: 999;
}

.navbar-container{
    display: flex;
    flex-direction: row;
    width: 1500px;
    height: 80px;
    justify-content: center;
    align-items: center;
}

.logo{
    text-decoration: none;
    font-size: 1.5rem;
    color: black;
    font-weight: 800;
    margin-right: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.menu-icons{
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
    padding: 10px;
    height: 80px;
}

.menu-icons:hover{
    color: chocolate;
    cursor: pointer;
}

.menu-items{
    margin-left: 10rem;
    align-items: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-button{
    margin-left: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    width: 150px;
    padding: 10px;
    border-radius: 20px;
    font-size: 1rem;
    text-decoration: none;
}

.nav-button:hover{
    background-color: white;
    color: black;
    cursor: pointer;
    border: 1px solid black;
}

.page{
    background-color: rgb(18, 20, 34);
    height: 100vh;
    width: 100%;
    display: flex;
    overflow: hidden;
}
.page1{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 50%;
    padding: 20px;
    align-items: center;
    justify-content: center;
}
.page2{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 50%;
}
.page2 img{
    display: flex;
    margin-top: 50px;
}
.header{
    color: whitesmoke;
    font-size: 4rem;
    margin-left: 2rem;
    margin-top: 30px;
}
.sub-header{
    color: whitesmoke;
    margin-left: 2rem;
}
.page-button{
    background-color: rgb(139, 40, 40);
    color: white;
    border: 0;
    padding: 10px;
    border-radius: 20px;
    width: 200px;
    font-size: 1rem;
    margin-top: 15px;
    align-self: flex-start;
    margin-left: 2rem;
    text-decoration: none;
    text-align: center;
}
.page-button:hover{
    background-color: whitesmoke;
    color: rgb(139, 40, 40);
}
.bar-icons{
    border: none;
    font-size: 1.5rem;
    display: none;
}


@media only screen and (max-width: 1024px) {
    .menu-items{
        display: none;
    }
    .bar-icons{
        border: none;
        font-size: 1.5rem;
        display: flex;
        margin-right: 2rem;
    }
    .navbar-container{
        justify-content: space-between;
        align-items: center;
        display: flex;
    }
    .logo{
        margin-left: 2rem;
    }
    .menu-items{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        margin-left: 0;
        width: 100%;
        height: 85vh;
        top: 80px;
        left: -100%;
        transition: all 0.5s ease;
        opacity: 1;
    }
    .menu-items.active{
        background-color: black;
        width: 100%;
        left: 0;
        transition: all 0.5s ease;  
        opacity: 1;
        z-index: 1;
    }
    .menu-icons{
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        text-align: center;
        margin-right: 0;
        width: 100%;
    }
    .menu-icons:hover{
        background-color: white;
        color: black;
    }
    .nav-button{
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: black;
        color: white;
        padding: 10px;
        border-radius: 25px;
        border: 1px solid white;
        width: 350px;
        height: 50px;
        font-size: 1rem; 
        margin-top: 10px;
        margin-left: 0;
        text-align: center;
    }
    .nav-button:hover{
        background-color: white;
        cursor: pointer;
        border-width: 0px;
        border-style: solid;
    }
    .page2{
    display: none;
        
    }
    .page1{
        display: flex;
        flex-direction: column;
        
        width: 80%;
        height: 100vh;
    }
    .page{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .header{
        font-size: 4rem;
    }
    .sub-header{
        font-size: 1rem;
    }
    .page-button{
    align-self: flex-start;
    }
}
