.page-background{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(18, 20, 34);
    width: 100%;
    height: 100vh;
}

.sub-background{
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    width: 450px;
    height: 80vh;
    padding: 10px;
    margin: auto;
}

.text{
    display: flex;
    align-self: center;
    font-size: 48px;
    font-weight: 700;
    color: rgb(18, 20, 34);
    margin-top: 40px;
}

.underline{
    display: flex;
    align-self: center;
    width: 60px;
    height: 8px;
    border-radius: 4px;
    background-color: rgb(18, 20, 34);
    margin-top: 5PX;
}

.inputs{
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 40px;
}
.input{
    display: flex;
    background-color: #eaeaea;
    height: 60px;
    align-items: center;
    justify-content: center;
    margin: auto;
    width: 400px;
    border-radius: 6px;
}
.input input{
    display: flex;
    height: 25px;
    width: 340px;
    background: transparent;
    outline: none;
    border: none;
    margin-left: 10px;
    font-size: 15px;
}
.forget-password{
    display: flex;
    margin: auto;
    width: 380px;
}
.forget-password span{
    color: blue;
    cursor: pointer;
}
.btns{
    display: flex;
}
.btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(18, 20, 34);
    height: 40px;
    border-radius: 25px;
    width: 180px;
    margin: auto;
    margin-top: 40px;
    cursor: pointer;
    color: white;
    font-size: 20px;
    font-weight: 500;
}
.btn.gray {
    background-color: #eaeaea;
    color: rgb(18, 20, 34);
}


