.top-container{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.mySwiper{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: auto;
    background-color: rgb(18, 20, 34);
    padding: 20px;
}
.model1{
    display: flex;
    height: 420px;
    width: 50%;
    margin-top: 20px;
}
.text-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;  
    height: 400px;
    margin-left: 1rem;
}
.header-1{
    display: flex;
    color: whitesmoke;
    font-size: 2.5rem;
    margin-left: 4rem;
}
.sub-header-1{
    display: flex;
    color: white;
    font-size: 1rem;
    margin-left: 4rem;
}
.btn-1{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    font-size: 17px;
    background-color: rgb(139, 40, 40);
    margin-left: 4rem;
    width: 150px;
    height: 40px;
    margin-top: 10px;
    border-radius: 20px;
}
.btn-1:hover{
    background-color: whitesmoke;
    color: rgb(139, 40, 40);
}
.model1 img{
    display: flex;
    margin-left: 90px;
}
.cat{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 80px;
}
.cat-image{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: whitesmoke;
    width: 150px;
    height: 150px;
    padding: 20px;
    transform: scale(1);
    transition: transform 0.2s ease;
}
.cat-image:hover{
    cursor: pointer;
    transform: scale(1.1);
}

.cat-lists{
    display: grid;
    grid-template-columns: repeat(4,auto);
    align-items: center;
    justify-content: center;
    background-color: #eaeaea;
    gap: 20px;
    width: 100%;
    height: 55vh;
    padding: 10px;
}
.cat-image img{
    display: flex;
    width: 100px;
    height: 100px;
}
.cat-image label{
    display: flex;
    font-size: 1rem;
    margin-top: 10px;
    font-weight: 500;
}
.arrival-container{
    display: grid;
    grid-template-columns: repeat(6,auto);
    gap: 10px;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: #eaeaea;
    margin: auto;
    overflow: auto;
    scroll-behavior: smooth;
    
}
.arrival-list{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color:  whitesmoke;
    width: 200px;
    height: 280px;
    border-radius: 10px;
    padding-bottom: 10px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    transform: scale(1);
    transition: transform 0.2s ease;
    overflow: hidden;


}
.arrival-list:hover{
    cursor: pointer;
    transform: scale(1.05);
}
.arrival-image{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eaeaea;
    width: 200px;
    height: 185px;
}
.arrival-image img{
    display: flex;
    width: 200px;
    height: 185px;
}
.discount{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 0;
    top: 0;
    background-color: rgb(139, 40, 40);
    font-weight: 600;
    color: whitesmoke;
    width: 50px;
    height: 30px;
}
.price{
    display: flex;
    font-weight: 500;
    font-size: 1.1rem;
    color: rgb(139, 40, 40);
    margin-top: 5px;
}
.equ{
    display: flex;
    color: rgb(139, 40, 40);
    font-weight: 700;
    font-size: 1.1rem;
}
.arrival-list h3{
    display: flex;
    margin-top: 10px;
}
.arrival-btn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.2rem;
    color: rgb(18, 20, 34);
    height: 40px;
    background-color: #eaeaea;
}
.link{
    text-decoration: none;
    color: black;
    align-self: flex-end;
    margin-right: 4.5rem;
}
.news-letter{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 20vh;
    margin: auto;
}
.news-header{
    display: flex;
    justify-content: center;
    align-items: center;
}
.news-header label{
    display: flex;
    margin-left: 5px;
    font-size: 1.2rem;
}
.news-input{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    margin: 10px;
}
.news-input input{
    display: flex;
}
.email-input{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    background-color: #eaeaea;
    width: 50%;
    height: 50px;
    border-radius: 25px;
}
.email-input input     {
    width: 95%;
    outline: none;
    border: none;
    background: transparent;
    font-size: 1.2rem;
}
.news-btn{
    display: flex;
    width: 120px;
    height: 50px;
    border-radius: 25px;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: 700;
    background-color: rgb(18, 20, 34);
    outline: none;
    border: 0;
    color: whitesmoke;
}
.news-btn:hover{
    cursor: pointer;
    background-color: white;
    border: 2px solid rgb(18, 20, 34);
    color: rgb(18, 20, 34);

}