.search{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eaeaea;
    height: 40px;
    width: 250px;
    border-radius: 25px;
    margin-left: 2rem;
}

.search input{
    display: flex;
    outline: none;
    background: transparent;
    border: none;
    height: 30px;
    width: 200px;
    margin-left: 3px;
    font-size: 15px;
}
.icons{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1rem;
    font-size: 1.2rem;
}

.user-icon{
    display: flex;
    font-size: 1.5rem;
    margin-left: 10px;
} 
.order-counts{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7rem;
    font-weight: 700;
    background-color: rgb(139, 40, 40);
    color: white;
    max-width: 40px;
    height: 20px;
    border-radius: 10px;
    padding: 5px;
}
.username{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
    font-weight: 600;
}

@media only screen and (max-width: 1024px) {
    .search{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #eaeaea;
        height: 40px;
        width: 250px;
        border-radius: 25px;
        margin-left: 15px;
    }
    .search input{
        display: flex;
        outline: none;
        background: transparent;
        border: none;
        height: 30px;
        width: 150px;
        margin-left: 3px;
        font-size: 15px;
    }
    .logo{
        display: flex;
        margin-right: 0;
        margin-left: 1rem;
    }
    .icons{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 5px;
        font-size: 1.2rem;
    }
    .user-icon{
        display: flex;
        font-size: 1.5rem;
        margin-left: 0;
        margin: 5px;
    }
    .username{
        display: none;
    } 
    .order-counts.mobile{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
        font-size: 0.6rem;
        font-weight: 700;
        background-color: rgb(139, 40, 40);
        color: white;
        max-width: 40px;
        height: 20px;
        border-radius: 10px;
        padding: 5px;
    }
    .bar-icons{
        margin-right: 15px;
    }
    
}