.footer{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   background-color: rgb(18, 20, 34);
   width: 100%;
   height: 65vh;
   margin: auto;
   padding: 15px;
}
.footer label{
   color: whitesmoke;
   font-size: 15px;
}
.footer-main{
   display: flex;
   justify-content: center;
}
.footer-div{
   display: flex;
   flex-direction: column;
   margin: 60px;
}
.logo-footer{
   display: flex;
   justify-content: center;
   align-items: center;
   text-decoration: none;
   font-size: 1.5rem;
   font-weight: 600;
   color: whitesmoke;
}
.social-icons{
   display: flex;
   gap: 10px;
   font-size: 1.5rem;
   color: whitesmoke;
   margin-top: 1rem;
}
.social-icons:hover{
   cursor: pointer;
}
.footer-div h3{
   color: gray;
}
.footer-text{
   display: flex;
   color: whitesmoke;
   text-decoration: none;
   font-size: 16px;
   margin-top: 3px;
}
.footer-text:hover{
   color: chocolate;
}
.footer-text.top{
   margin-top: 1rem;
}

@media only screen and (max-width:1024px) {
   .footer-div{
      display: flex;
      flex-direction: column;
      margin: 20px;
   }
   .footer label{
      margin-top: 35px;
   }
}